.circle-container {
    /* 圆环 */
    position: relative;
    width: 206px;
    height: 206px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 28px;

    .circle {
        position: absolute;
        top: 0;
        left: 0;
        width: 206px;
        height: 206px;
        border-radius: 50%;
        background: linear-gradient(0deg, rgb(255, 220, 0) 0%, rgb(175, 0, 229) 100%);
        -webkit-mask: radial-gradient(transparent, transparent 97px, #000 97px);
        mask: radial-gradient(transparent 97px, #000 97px);
    }

    .circle-text {
        position: relative;
        z-index: 1;
        text-align: center;
        font-size: 36px;
        font-weight: 900;
        color: #FFFFFF;
    }
    .circle-text1 {
        position: relative;
        z-index: 1;
        text-align: center;
        font-size: 16px;
        display: flex;
        flex-direction: column;
        align-items: center;
        font-weight: 900;
        color: #FFFFFF;
    }

}

@keyframes rotate {
    from {
        transform: rotate(0deg);
        /* 起始角度为0度 */
    }

    to {
        transform: rotate(360deg);
        /* 终止角度为360度，即一圈 */
    }
}

.same-qiu {
    position: absolute;

    &>img {
        width: 193px;
        animation: rotate 10s infinite linear;
        /* 设置旋转动画 */
    }
}

