/* 水波纹 */
.border2:hover:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    animation: ripple 1s ease-out;
}

.other-img::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: -9px;
    left: -8px;
    background-image: radial-gradient(circle, rgba(255, 220, 0, .5) 0%, rgba(175, 0, 229, .9) 80%);
    border-radius: 50%;
    animation: ripple 2s ease-out infinite;
}

@keyframes ripple {
    0% {
        transform: scale(1);
        opacity: 1;
    }

    100% {
        transform: scale(2);
        opacity: 0;
    }
}

.same-line {
    height: 2px;
